<template>
  <div class="panel panel-order-detail">
    <el-card>
      <div slot="header">
        <span class="card-name">基本信息</span>
      </div>
      <ul>
        <li><span class="title">订单编号：</span><span class="content">{{info.orderNo}}</span></li>
        <li><span class="title">订单时间：</span><span class="content">{{info.orderTime}}</span></li>
        <li><span class="title">课程名称：</span><span class="content">{{info.productName}}</span></li>
        <li><span class="title">身份证号：</span><span class="content">{{cust.idCardNo}}</span></li>
        <li><span class="title">姓名：</span><span class="content">{{cust.username}}</span></li>
        <li><span class="title">邮箱：</span><span class="content">{{cust.email}}</span></li>
        <li><span class="title">手机号码：</span><span class="content">{{cust.mobile}}</span></li>
      </ul>
    </el-card>
    <el-card>
      <div slot="header">
        <span class="card-name">代扣信息</span>
      </div>
      <ul>
        <li><span class="title">总金额：</span><span class="content">{{info.amount / 100}}</span></li>
        <li><span class="title">首付金额：</span><span class="content">{{(info.downPayment / 100).toFixed(2)}}</span></li>
        <li><span class="title">订单类型：</span><span class="content">{{info.orderType}}</span></li>
        <li><span class="title">首付账户：</span><span class="content">{{info.downPaymentType}}</span></li>
        <li><span class="title">代扣金额：</span><span class="content">{{(info.remainderAmt / 100).toFixed(2)}}</span></li>
        <li><span class="title">代扣期数：</span><span class="content">{{info.remainderPeriod}}</span></li>
        <li><span class="title">扣款账户：</span><span class="content">{{info.renewAccount}}</span></li>
        <li><span class="title">合同：</span><span class="content btn-download">合同下载</span></li>
      </ul>
    </el-card>
    <el-table :data="bills" cell-class-name="cell-container" header-cell-class-name="cell-container" header-row-class-name="table-header">
      <el-table-column prop="period" label="期数" align="center">
      </el-table-column>
      <el-table-column prop="billDate" label="应扣日期" align="center">
      </el-table-column>
      <el-table-column prop="renewDate" label="实扣日期" align="center">
      </el-table-column>
      <el-table-column prop="billAmt" label="应还金额" align="center">
        <template slot-scope="scope">
          <span>{{(scope.row.billAmt / 100).toFixed(2)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="payAmount" label="实还金额" align="center">
        <template slot-scope="scope">
          <span>{{scope.row.payAmount && (scope.row.payAmount / 100).toFixed(2)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="状态" align="center">
        <template slot-scope="scope">
          <span>{{scope.row.status | renewStatus}}</span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { getOrderDetail } from '@/api/index.js';
import dayjs from 'dayjs';

export default {
  name: 'order-detail',
  props: ['id'],
  data() {
    return {
      bills: [],
      cust: {},
      info: {},
    };
  },
  created() {
    this.orderId = this.$route.params.id;
    this.queryData();
  },
  methods: {
    queryData() {
      getOrderDetail(this.orderId)
        .then((result) => {
          if (result) {
            if (result.info) {
              result.info.orderTime = dayjs(result.info.orderTime).format('YYYY-MM-DD hh:MM:ss');
              this.info = result.info;
            }
            this.cust = result.cust || {};
            if (Array.isArray(result.bill) && result.bill.length > 0) {
              this.bills = result.bill.slice(1);
            }
          }
        })
        .catch(err => console.error(err));
    },
  },
};
</script>

